export class DateUtils {
  /**
   * Convert a date to a string "YYYY-MM-DD"
   * @param d
   * @returns
   */
  public static getDateInYYYY_MM_DD_Format(d: Date) {
    return d.toISOString().substring(0, 10);
  }

  /**
   * Convert a date to a string "HH:MM" using 24 hour
   * @param d
   * @returns
   */
  public static getTimeInHH_MM_Format(d: Date) {
    const hours = d.getHours();
    const minutes = d.getMinutes();
    return hours.toString() + ':' + minutes.toString();
  }

  public static convertDateTimeIntoDate(date: string, time: string) {
    const d: Date = new Date(date);
    const splitTime = time.split(':');
    const hours = parseInt(splitTime[0]);
    if (hours !== NaN) d.setHours(hours);
    const minutes = parseInt(splitTime[1]);
    if (minutes !== NaN) d.setMinutes(minutes);
    return date;
  }
}
