import React, { useState } from "react";
import { Card, Input, InputAdornment, TextField } from "@material-ui/core";
import SearchIcon from '@material-ui/icons/Search';
import { getDb } from "../../context/AuthProvider";
import { Course } from "golf-gamblers-model"
import { useBreadCrumbs } from "../../context/BreadcrumbsProvider";
interface FirebaseObject<T> {
    id: string;
    value: T;
}
interface ISelectCourseProps {
    goBack: () => void;
    selectCourse: (courseId: string, courseName: string) => void;
}
export default function SelectCourse(props: ISelectCourseProps) {
    const db = getDb();
    const breadCrumbs = useBreadCrumbs();
    const [prevSearchText, setPrevSearchText] = useState("")
    const [searchText, setSearchText] = useState("")
    const [courseResults, setCourseResults] = useState<FirebaseObject<Course>[]>([]);
    const [lastSearchedTimeStampMs, setLastSearchedTimeStampMs] = useState(new Date().getUTCMilliseconds());
    const [timer, setTimer] = useState<NodeJS.Timeout>()

    const delayBeforeSearchMs = 400;

    async function searchForCourseName(newSearchText: string) {
        const query = db.collection('courses').where('name', '>=', newSearchText).where('name', '<=', newSearchText + '\uf8ff').limit(25);
        const courseSearchResults = await query.get();
        const searchResultsArr = [];
        for (let i = 0; i < courseSearchResults.docs.length; i++) {
            const doc = courseSearchResults.docs[i];
            const course = doc.data() as Course;
            searchResultsArr.push({ id: doc.id, value: course });
        }
        setCourseResults(searchResultsArr);
    }

    function handleSearchTextChange(e: React.ChangeEvent<HTMLInputElement>) {
        setSearchText(e.target.value);
        // Clears running timer and starts a new one each time the user types
        if (timer) {
            clearTimeout(timer);
        }
        setTimer(setTimeout(() => {
            searchForCourseName(e.target.value);
        }, delayBeforeSearchMs))
    }
    function handleSelectCourse(courseId: string, courseName: string) {
        props.selectCourse(courseId, courseName);
        props.goBack();
    }
    return <div>
        <h2>Select Course</h2>
        <div><TextField type="text" value={searchText} onChange={handleSearchTextChange} InputProps={{
            startAdornment: (
                <InputAdornment position="start">
                    <SearchIcon />
                </InputAdornment>
            ),
        }}
        ></TextField></div>
        <div>
            <br></br>
            {courseResults.map((courseObj, i) => {
                const course = courseObj.value;
                return <div key={'course-' + i}>
                    <Card onClick={() => handleSelectCourse(courseObj.id, course.name)}>
                        <b>{course.name}</b>
                        <div>{course.location.country}, {course.location.city} {course.location.state}</div>
                    </Card>
                    <br></br>
                </div>
            })}
        </div>
    </div>
}
