import React, { useState } from "react";
import { Button, TextField } from "@material-ui/core";
import { DateUtils } from "../../common/utils/DateUtils";
import SelectCourse from "./SelectCourse";
import SelectGroup from "./SelectGroup";

export default function CreateEvent() {
    const [eventName, setEventName] = useState("");
    const [courseName, setCourseName] = useState("");
    const [courseId, setCourseId] = useState("");
    const [numberOfHoles, setNumberOfHoles] = useState(18);
    const [startingHole, setStartingHole] = useState(1);
    const [maxBet, setMaxBet] = useState(5);
    const [startDate, setStartDate] = useState(DateUtils.getDateInYYYY_MM_DD_Format(new Date()));
    const [startTime, setStartTime] = useState(DateUtils.getTimeInHH_MM_Format(new Date()));
    const [isSelectingCourse, setIsSelectingCourse] = useState(false);
    const [isSelectingGroup, setIsSelectingGroup] = useState(false);

    // handlers
    function handleEventNameChange(e: React.ChangeEvent<HTMLInputElement>) {
        setEventName(e.target.value);
    }
    function handleMaxBetChange(e: React.ChangeEvent<HTMLInputElement>) {
        const updated = parseInt(e.target.value);
        if (updated !== NaN) {
            console.log(updated)
            setMaxBet(updated);
        }
    }
    function handleStartDateChange(e: React.ChangeEvent<HTMLInputElement>) {
        setStartDate(e.target.value)
    }
    function handleStartTimeChange(e: React.ChangeEvent<HTMLInputElement>) {
        setStartTime(e.target.value);
    }
    function handleIsSelectingCourse(e: React.FocusEvent<HTMLInputElement>) {
        setIsSelectingCourse(true);
    }
    function handleGoBack() {
        setIsSelectingCourse(false);
    }
    function handleSelectCourse(courseId: string, courseName: string) {
        setCourseId(courseId);
        setCourseName(courseName);
    }
    function handleSelectGroup(courseId: string, courseName: string) {
        setCourseId(courseId);
        setCourseName(courseName);
    }

    if (isSelectingCourse) {
        return <SelectCourse goBack={handleGoBack} selectCourse={handleSelectCourse}></SelectCourse>;
    } else if (isSelectingGroup) {
        return <SelectGroup></SelectGroup>;
    } else {
        return <div>
            <h2>Create Event</h2>
            <div><TextField value={eventName} label="Event Name" onChange={handleEventNameChange}></TextField></div>
            <div><TextField value={courseName} label="Course" onChange={handleEventNameChange} onFocus={handleIsSelectingCourse}></TextField></div>
            <div><TextField type="number" value={maxBet} label="Max Down" onChange={handleMaxBetChange}></TextField></div>
            <div><TextField type="date" value={startDate} label="Date" onChange={handleStartDateChange}></TextField></div>
            <div><TextField type="time" value={startTime} label="Time" onChange={handleStartTimeChange}></TextField></div>
            <div><TextField type="time" value={startTime} label="Time" onChange={handleStartTimeChange}></TextField></div>
            <Button>Create Event</Button>
        </div>
    }


}