import { createContext, useContext, useState } from 'react';

let defaultBreadCrumbs: string[] = [];
const BreadCrumbsContext = createContext<{ state: string[], update: (newValue: string[]) => void }>({ state: defaultBreadCrumbs, update: (newValue) => { defaultBreadCrumbs = newValue } });

interface IBreadCrumbsContextProps {
    children: any
}

export function ThemeProvider(props: IBreadCrumbsContextProps) {
    const [breadCrumbs, setBreadCrumbs] = useState<string[]>(defaultBreadCrumbs);
    return <BreadCrumbsContext.Provider value={{ state: breadCrumbs, update: setBreadCrumbs }}>
        {props.children}
    </BreadCrumbsContext.Provider>;
}

export function useBreadCrumbs() {
    return useContext(BreadCrumbsContext);
}