import { Button } from "@material-ui/core";
import { Link } from "react-router-dom";
import { useSubscriptionContext, useUserContext } from "../context/AuthProvider";
import { useThemeContext } from "../context/ThemeProvider";

export default function Home() {
    const user = useUserContext();
    const subscriptionState = useSubscriptionContext();
    const theme = useThemeContext();

    return <div>
        {subscriptionState ? <div>
            <h3>Welcome {user?.displayName} !</h3>
            <h4>Events</h4>
            <div>
                <Link to="/create-event" style={{ color: theme.background_color }}>
                    <Button aria-label="create-event" color="inherit">Create Event</Button>
                </Link>
            </div>
        </div> :
            <div>
                <p>Update your account's subscription to see this content.</p>
            </div>
        }
    </div>
}